import React from "react";
import { NavLink } from "react-router-dom";
import { useAuth } from "../../context/Auth";
import { toast } from "react-toastify";
import SearchInput from "../Form/SearchInput";

const Header = () => {
  const [auth, setAuth] = useAuth();
  const handleLogout = () => {
    setAuth({
      ...auth,
      user: null,
      token: "",
    });
    localStorage.removeItem("auth");
    toast.success("Logout Successfully");
  };
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-dark">
        <div className="container-fluid">
          <NavLink to="/" className="navbar-brand">
            <h3 style={{ color: "white" }}>ninja_marketplace</h3>
          </NavLink>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
            style={{ backgroundColor: "white" }}
          >
            <span className="navbar-toggler-icon" />
          </button>
          <div
            className="collapse navbar-collapse"
            id="navbarSupportedContent"
            style={{ marginLeft: "8vw" }}
          >
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              {/* <li> */}
              <SearchInput />
              {/* </li> */}
              <li className="nav-item" style={{ marginLeft: "5vw" }}>
                <div className="dropdown">
                  <button
                    className="btn btn-secondary dropdown-toggle bg-dark"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                    style={{ border: "none" }}
                  >
                    Categories
                    <sup>
                      <i className="fa-solid fa-list-check" />
                    </sup>
                  </button>
                  <ul className="dropdown-menu">
                    <li>
                      <NavLink to="/" className="dropdown-item">
                        Electronics
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="/" className="dropdown-item">
                        Men's Fashion
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="/" className="dropdown-item">
                        Women's Fashion
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="/" className="dropdown-item">
                        Home, Kitchen, Grocery
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="/" className="dropdown-item">
                        Beauty, Health
                      </NavLink>
                    </li>
                    <li>
                      <NavLink to="/" className="dropdown-item">
                        Books
                      </NavLink>
                    </li>
                  </ul>
                </div>
              </li>
              <li className="nav-item" style={{ marginLeft: "5vw" }}>
                <NavLink
                  to="/"
                  className="nav-link"
                  aria-current="page"
                  style={{ color: "white" }}
                >
                  Orders
                  <sup>
                    <i className="fa-solid fa-tag" />
                  </sup>
                </NavLink>
              </li>
              <li className="nav-item" style={{ marginLeft: "5vw" }}>
                <NavLink to="/" className="nav-link" style={{ color: "white" }}>
                  <i
                    className="fa-solid fa-cart-plus"
                    style={{ color: "#1ff05b", fontSize: "15pt" }}
                  />
                  <sup>0</sup>
                </NavLink>
              </li>

              {!auth.user ? (
                <>
                  <li
                    className="nav-item"
                    style={{ marginLeft: "5vw", backgroundColor: "blue" }}
                  >
                    <NavLink
                      to="/login"
                      className="nav-link"
                      style={{ color: "white" }}
                    >
                      <i className="fa-solid fa-user" /> Register/Login
                    </NavLink>
                  </li>
                </>
              ) : (
                <>
                  <li
                    className="nav-item dropdown"
                    style={{ marginLeft: "5vw" }}
                  >
                    <NavLink
                      className="nav-link dropdown-toggle"
                      href="#"
                      role="button"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                      style={{ color: "white" }}
                    >
                      {auth?.user?.name}
                    </NavLink>
                    <ul className="dropdown-menu">
                      <li>
                        <NavLink
                          to={`/dashboard/${
                            auth?.user?.role === 1 ? "admin" : "user"
                          }`}
                          className="dropdown-item"
                        >
                          Dashboard
                        </NavLink>
                      </li>
                      <li>
                        <NavLink
                          onClick={handleLogout}
                          to="/login"
                          className="nav-link"
                          style={{ backgroundColor: "red", color: "white" }}
                        >
                          <i className="fa-solid fa-user" /> Logout
                        </NavLink>
                      </li>
                    </ul>
                  </li>
                </>
              )}

              {/* ===================== */}
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Header;
